:root {
  --green: #64ffda;
  --lightest-navy: #233554;
  --lightest-slate: #ccd6f6;
}
#root {
  display: grid;
  min-height: 100vh;
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
}
body {
  margin: 0;
  background-color: #0a192fd9;
  line-height: 1.3;
}
header {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
}
nav {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: center;
  width: 100%;
  height: 100px;
}
ol {
  display: flex;
  list-style: none;
  align-items: center;
  position: relative;
}
section{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100vh;
  justify-content: center;
  align-items: flex-start;
}
p{
  max-width: 690px;
    margin: 0px;
    font-size: 20px;
    margin-top: 20px;
    color: var(--lightest-slate);
}
h2 {
  color: white;
  padding-left: 35px;
  line-height: 1.1;
}
h3 {
  line-height: 0.9;
  color: var(--lightest-slate);
}
