.about-container {
  display: flex;
  justify-content: center;
}
.about__title{
    padding: 0px;
    margin: 0px;

}
.about__title::before{
    position: relative;
    counter-increment: section 1;
    content: "0" counter(section) ".";
    margin-right: 10px;
    font-weight: 400;
}
